#work {
  width: 100%;
  height: 100vh;
  padding: 8rem $p;
  > h2 {
    font: 700 2.5rem $text2;
    width: fit-content;
    border-bottom: 1px solid $color6_1;
    padding: 0.5rem;
    margin: auto;
  }

  .navigation-buttons{
    height:70px;
    display: flex;
    align-items: center;
    justify-content: center;
    :hover{
      background-color: rgb(46, 49, 46);
    }
    >button{
      height: 60px;
      width: 60px;
      border:2px solid rgb(161, 161, 230);
      border-radius: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 40px;
      
    }

  }
  .button1{
    margin-right: 130px;
    background-color: $color5;
  }
  .button2{
    background-color: $color5;
  }
  .button1 > img{
    height: 40px;
  }
  .button2 > img{
    height: 40px;
  }
  .projectheading{
    margin-top: 60px;
  }

  > section {
    margin-top: 30px;
    width: 100%;
    height: 110%;
    background-color: $color5;
    display: flex;
    align-items: center;
    justify-content: center;

    > article {
      width: 70%;
      height: 27vmax;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
    }

    .workItem {
      overflow-y: auto;
      height: 25vmax;
      width: 95%;
      background-color: $color4;
      display: flex;
      padding: 2rem;
      box-shadow: 0 0 20px 5px $color1;

      > img {
        border-radius: 20px;
        width: 40%;
      }

      > aside {
        width: 60%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        > p {
          line-height: 180%;
          margin: 2rem;
        }
        > a {
          background-color: $color3;
          padding: 0.7rem 2rem;
          margin: 2rem;
          color: $color4;
          transition: all 0.5s;
          &:hover {
            background-color: $color3_1;
          }
        }
      }
    }
  }
}
